import React, { useState, useEffect } from "react";

import { useInView } from "react-intersection-observer";

import "./CitiesSkills.css";

export default function CitiesSkills(prop) {

  const [refProgress, inViewProgress] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const [progress, setProgress] = useState(0);

  useEffect(() => {
    if (inViewProgress) {
      const timer = setInterval(() => {
        setProgress((prevProgress) => {
          if (prevProgress >= 88) {
            clearInterval(timer);
          }
          return prevProgress + 1;
        });
      }, 10);

      return () => {
        clearInterval(timer);
      };
    }
  }, [inViewProgress]);

  const [refDevelopment, inViewDevelopment] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const [development, setDevelopment] = useState(0);

  useEffect(() => {
    if (inViewDevelopment) {
      const timer = setInterval(() => {
        setDevelopment((prevProgress) => {
          if (prevProgress >= 76) {
            clearInterval(timer);
          }
          return prevProgress + 1;
        });
      }, 10);

      return () => {
        clearInterval(timer);
      };
    }
  }, [inViewDevelopment]);

  const [refMarketing, inViewMarketing] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const [marketing, setMarketing] = useState(0);

  useEffect(() => {
    if (inViewMarketing) {
      const timer = setInterval(() => {
        setMarketing((prevProgress) => {
          if (prevProgress >= 93) {
            clearInterval(timer);
          }
          return prevProgress + 1;
        });
      }, 10);

      return () => {
        clearInterval(timer);
      };
    }
  }, [inViewMarketing]);

  return (
    <div className="cities-skills-container">
      <div className="cities-skills-1">
        <h1>OUR SKILLS</h1>

        <br />

        <p>
          {prop.paragraph}
        </p>

        <br />

        <button>Call Now</button>
      </div>

      <div className="cities-skills-2">
        <div>
          <h1>Design</h1>
          <div className="cities-progress-bar-container">
            <div
              className="cities-progress-bar"
              style={{ width: `${progress}%` }}
              ref={refProgress}
            >
              {progress}%
            </div>
          </div>
        </div>

        <div>
          <h1>Development</h1>
          <div className="cities-progress-bar-container">
            <div
              className="cities-progress-bar"
              style={{ width: `${development}%` }}
              ref={refDevelopment}
            >
              {development}%
            </div>
          </div>
        </div>

        <div>
          <h1>Marketing</h1>
          <div className="cities-progress-bar-container">
            <div
              className="cities-progress-bar"
              style={{ width: `${marketing}%` }}
              ref={refMarketing}
            >
              {marketing}%
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
