import React, { useEffect } from "react";

import "./Cleveland.css";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import CityBanner from "../../../components/cities/cities-banner/CityBanner.js";

import CitiesCardsTwo from "../../../components/cities/cities-cards-two/CitiesCardsTwo";

import CitiesSkills from "../../../components/cities/cities-skills/CitiesSkills";

import PortfolioWorks from "../../../components/portfolio-components/portfolio-works/PortfolioWorks";

import Footer from "../../../components/genral_components/Footer/Footer";

import { useLocation } from "react-router-dom";

export default function Cleveland() {
  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);

  let text = ["Digital Marketing Agency In",
 <br/>,
 "Cleveland"];

  return (
    <div className="Cleveland-main-container">
      <Navbar />

      <CityBanner cityBannerHeading={text} />

      <CitiesCardsTwo
        headingOne="Video Marketing in Cleveland"
        paragraphOne="We use appealing videos to ensure that your company stands out. Our video marketing company in Cleveland creates exciting, engaging videos that effectively convey your message!"
        headingtwo="Graphic Designing in Cleveland"
        paragraphTwo="Want to use captivating and fascinating designs to communicate a brand's message to its intended audience? You can count on our outstanding graphic design services, Given that we are one of Cleveland's most reputable graphic design companies, you can rely on our high-quality services."
        headingThree="Web Design in Cleveland"
        paragraphThree="We at our web design agency in Cleveland employ design innovation to create a brand personality that will resonate with each visitor. We provide a number of design alternatives from the start, with the objective of creating a website that suits your brand and speaks to all of your users."
        headingFour="Social Media Marketing In Cleveland"
        paragraphFour="We provide thorough tactics and services as a social media marketing company in Cleveland to improve your online visibility, to increase brand exposure, engagement, and conversion, our team of professionals excels at content production, audience targeting, and analytics. We help businesses in Cleveland navigate the digital space effectively and achieve their marketing goals!"
        paragraphFive="We are a renowned digital marketing agency in Cleveland that helps you in raising the visibility of your brand on search engines. The greatest creative design and marketing services are provided by our top-tier digital marketing agency in Cleveland. You can streamline your business and reach the right audience with our top-notch digital tactics. Utilizing our innovative approaches, showcase your brand in the constantly evolving digital world while reaching your objectives."
      />

      <CitiesSkills paragraph="As a devoted digital marketing agency in Cleveland, our skills are finely honed in various aspects of the industry. We specialize in providing exceptional services as a social media marketing agency, excelling in strategies for Facebook business marketing and other social media platforms. We possess a diverse range of proficiencies across various digital marketing disciplines, surpassing the scope of social media marketing. With our extensive repertoire of skills, we can elevate your online visibility, amplify brand recognition, and achieve exceptional outcomes for your business." />

      <PortfolioWorks />

      <br />
      <br />

      <Footer />
    </div>
  );
}
