import React from "react";

import "./cardThree.css";

import pic1 from "./pic1.jpg";

import { Link } from "react-router-dom";

export default function CardThree(prop) {
  const isOdd = (cardNumber) => {
    if (cardNumber % 2 == 0) {
      return false;
    } else {
      return true;
    }
  };

  return (
    <div
      className="card-three-main-container"
      style={{ flexDirection: isOdd(prop.cardNumber) ? "row-reverse" : "row" }}
    >
      <div className="card-three-main-container-first-box">
        <h1>{prop.heading}</h1>

        <p>{prop.paragraph}</p>

        <Link to="/" ><button>Learn More</button></Link>
      </div>

      <div className="card-three-main-container-second-box">
        <img src={prop.backgroundImage} />
      </div>
    </div>
  );
}

CardThree.defaultProps = {
  backgroundImage: pic1,
};
