import React from 'react'

import "./CityBanner.css"

import statue from "./statue.mp4";

export default function CityBanner(prop) {

  const bannerVideo = prop.bannerVideo || statue;

  return (
    <div className="cities-video-background">
      <video autoPlay muted loop id="cities-my-video">
        <source src={bannerVideo} type="video/mp4" />
      </video>
      <div className="cities-content">
        {/* Your content goes here */}
        <h1>
            {prop.cityBannerHeading}
        </h1>

      </div>
    </div>
  )
}

CityBanner.defaultProps = {
  bannerVideo : statue
}