import React from "react";

import "./CitiesCardsTwo.css";

import CardTwo from "../card-two/CardTwo";

import pic1 from "./pic1.jpg";

import CardThree from "../card-three/CardThree";

export default function CitiesCardsTwo(prop) {
  return (
    <div className="cities-cards-two-main-container">

      <CardThree
        backgroundImage={prop.pic1}
        heading={prop.headingOne}
        paragraph={prop.paragraphOne}
        src="/services/videomarketingservices"
        cardNumber={1}
      />

      <CardThree
        backgroundImage={prop.pic1}
        heading={prop.headingtwo}
        paragraph={prop.paragraphTwo}
        src="/services/graphicdesign"
        cardNumber={2}
      />

      <CardThree
        backgroundImage={prop.pic1}
        heading={prop.headingThree}
        paragraph={prop.paragraphThree}
        src="/services/websiteDesignAndDevelopment"
        cardNumber={3}
      />

      <CardThree
        backgroundImage={prop.pic1}
        heading={prop.headingFour}
        paragraph={prop.paragraphFour}
        src="/services/socialmediamarketing"
        cardNumber={4}
      />

      <CardThree
        backgroundImage={prop.pic1}
        heading={prop.headingFive}
        paragraph={prop.paragraphFive}
        src="/"
        cardNumber={5}
      />

    </div>
  );
}

CitiesCardsTwo.defaultProps = {
  pic1: pic1,
};
