import React from "react";

import "./PortfolioModalTwo.css";

export default function PortfolioModalTwo(prop) {
  return (
    <div
      className="portfolio-modal-two-main-container-first"
      onClick={() => {
        prop.closeModal();
      }}
    >
      <div className="portfolio-modal-two-main-container" style={{overflowY : (prop.category == "Web Design") ? "scroll" : "" }} >
        <img src={prop.picture} alt="work" style={{maxHeight : (prop.category == "Web Design") ? "500vh" : "80vh"}} />
      </div>
    </div>
  );
}
